
.Node{border:solid;border-color:grey;border-width:1px;background-color:#fff}.Node:hover{cursor:pointer}
.Tree{position:relative;display:flex;justify-content:space-evenly;flex-direction:column;height:400px}@-webkit-keyframes activate{0%{border-color:grey;stroke:grey}to{border-color:gold;stroke:gold}}@keyframes activate{0%{border-color:grey;stroke:grey}to{border-color:gold;stroke:gold}}.Tree .active{-webkit-animation:activate 1.5s forwards;animation:activate 1.5s forwards}.Tree-Row{display:flex;justify-content:space-evenly;height:64px}
.Tree-Links{position:absolute;top:0;left:0;width:100%;height:100%}.Tree-Links>path{fill:none;stroke:grey;stroke-width:6px}